body {
    background-color: #1e2931;
    color: #fff;
    width: 100%;

 
  }
.search {
    height:45px;
    width:80%;
    background-color: #fff;
    display: block;
    margin: auto;
    font-size: 18px;
    text-align: center;
    margin-top: 30px;
    
}

.movies{
  
        display: inline-block;
        margin-top: 50px;
        padding: 10px;
        margin-left: 30px;

      
      }
    
.title{
  
        font-size: 20px;
        font-weight: normal;
        font-variant: small-caps;
       margin-left: 50px;
       visibility: hidden;
      
}

#img {
    height: 300px;
    width: 300px;   
   
}


  .movies:hover .title{
    visibility: visible;
}
